.header { position: relative; z-index: 10; }

.header-list { align-items: center; display: flex; flex-direction: row; flex-wrap: wrap; height: 80px; justify-content: space-around; }
.header-list__link { color: #435993; }
.header-list__link:hover { color: #1c1c1c; }

.form { position: relative; text-align: center; z-index: 3; }
.form__title-primary { margin: 120px 0 0; }

.list { padding: 70px 0 0; }
.list__title { position: relative; padding: 30px 0 0; }
.list__title:before { content: ""; display: block; position: absolute; bottom: 100%; left: 0; right: 0; margin: auto; }
.list__title:nth-of-type(1):before { background-image: url("../img/icons.png"); width: 47px; height: 47px; background-position: 0px -51px; }
.list__title:nth-of-type(2):before { background-image: url("../img/icons.png"); width: 47px; height: 47px; background-position: -51px 0px; }
.list__title:nth-of-type(3):before { background-image: url("../img/icons.png"); width: 47px; height: 47px; background-position: -51px -51px; }
.list__title:nth-of-type(4):before { background-image: url("../img/icons.png"); width: 47px; height: 47px; background-position: 0px 0px; }
.list__content { padding: 50px 0 70px; }


